 border-box;\n    background: #fff;\n    margin-bottom: 20px;\n    width: 100%;\n    min-height: 120px;\n    box-shadow: 1px 1px 2px 2px rgba($color: #bbb, $alpha: 0.3);\n    border-radius: 10px;\n    &.focus {\n      border: 1px solid $theme-blue;\n      box-shadow: 1px 1px 2px 2px #cbe6ff;\n      flex: 1;\n      .emily-tpl-editor-section-tool-bar {\n        display: flex;\n      }\n    }\n    &.set {\n      .emily-tpl-editor-section-form {\n        display: none;\n      }\n      .emily-tpl-editor-section-container {\n        display: flex;\n        .emily-tpl-section-wrap {\n          flex: 1;\n          position: relative;\n          padding: 20px 20px 20px 180px;\n          overflow: hidden;\n          .section-name {\n            position: absolute;\n            top: 0;\n            left: 0;\n            width: 150px;\n            height: 100%;\n            display: flex;\n            background: $theme-blue;\n            border-radius: 10px 0 0 10px;\n            justify-content: center;\n            align-items: center;\n            color: #fff;\n          }\n        }\n      }\n\n      .tool-btn.copy {\n        display: flex !important;\n      }\n    }\n    .emily-tpl-editor-section-form {\n      flex: 1;\n      display: flex;\n      justify-content: center;\n      align-items: center;\n      .flex-item {\n        label {\n          font-size: 14px;\n          color: #666;\n        }\n      }\n    }\n    .emily-tpl-editor-section-container {\n      display: none;\n      flex: 1;\n    }\n    .emily-tpl-editor-section-tool-bar {\n      display: none;\n      position: absolute;\n      left: 50%;\n      transform: translate(-50%, 0);\n      border-radius: 24px;\n      background-color: $theme-blue;\n      overflow: hidden;\n      z-index: 999;\n      &.top {\n        top: -12px;\n      }\n      &.bottom {\n        bottom: -12px;\n      }\n      li.tool-btn {\n        flex: 1;\n        width: 30px;\n        height: 24px;\n        display: flex;\n        justify-content: center;\n        align-items: center;\n        list-style: none;\n        cursor: pointer;\n        &:hover {\n          background: $theme-blue-light;\n        }\n        .icon {\n          width: 18px;\n          height: 18px;\n        }\n        &.add {\n          .icon {\n            background: url(\"../icons/add.svg\");\n            background-size: 100% 100%;\n          }\n        }\n        &.copy {\n          display: none;\n          .icon {\n            transform: scale(0.8);\n            background: url(\"../icons/copy.svg\");\n            background-size: 100% 100%;\n          }\n        }\n        &.delete {\n          .icon {\n            background: url(\"../icons/delete.svg\");\n            background-size: 100% 100%;\n          }\n        }\n      }\n    }\n  }\n}\n.emily-tpl-input {\n  margin-right: 40px;\n  padding: 5px 11px;\n  width: 200px;\n  height: 30px;\n  border: 1px solid #d9d9d9;\n  box-sizing: border-box;\n  outline: none;\n  border-radius: 4px;\n  transition: all 0.3s;\n  &:hover {\n    border: 1px solid $theme-blue;\n  }\n  &:focus {\n    border: 1px solid $theme-blue;\n    box-shadow: 0 0 2px 2px #cbe6ff;\n  }\n  &::-webkit-input-placeholder {\n    color: #ccc;\n  }\n}\n.emily-tpl-select {\n  @extend .emily-tpl-input;\n  height: 30px;\n  cursor: pointer;\n}\n.emily-tpl-btn {\n  min-width: 80px;\n  height: 30px;\n  border-radius: 4px;\n  outline: none;\n  border: none;\n  color: #fff;\n  cursor: pointer;\n\n  &.normal {\n    background: $theme-blue;\n    &:hover {\n      background: $theme-blue-light;\n    }\n  }\n}\n","$theme-main-color:#fff;\n$theme-blue:#1890ff;\n$theme-blue-light:#63b1f9"],"sourceRoot":""}]);
